import classNames from 'classnames/bind';
import styles from '@components/sections/Share.module.scss';
import Section from '@shared/Section';
import { useEffect } from 'react';
import { parseISO, format } from 'date-fns';
import { ko } from 'date-fns/locale';
import { CopyToClipboard } from 'react-copy-to-clipboard';

declare global {
  interface Window {
    Kakao: any;
  }
}
const cx = classNames.bind(styles);

interface ShareProps {
  groomName: string;
  brideName: string;
  date: string;
}
function Share({ groomName, brideName, date }: ShareProps) {
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://t1.kakaocdn.net/kakao_js_sdk/2.7.2/kakao.min.js';
    script.async = true;

    document.head.appendChild(script);

    script.onload = () => {
      console.log(window);

      if (!window.Kakao.isInitialized()) {
        window.Kakao.init(process.env.REACT_APP_KAKAO_APP_KEY);
      }
    };
  }, []);

  const handleHareKakao = () => {
    window.Kakao.Share.sendDefault({
      objectType: 'feed',
      content: {
        title: `${groomName} ${brideName} 결혼합니다`,
        description: `${format(parseISO(date), 'M월 d일 eeee aaa h시', { locale: ko })}`,
        imageUrl:
          'data:image/png;base64,',
        link: {
          mobileWebUrl: window.location.origin,
          webUrl: window.location.origin,
        },
      },
      buttons: [
        {
          title: '청첩장 보기',
          link: {
            mobileWebUrl: window.location.origin,
            webUrl: window.location.origin,
          },
        },
      ],
    });
  };

  return (
    <Section title="공유하기">
      <div className={cx('wrap-share')}>
        <button onClick={handleHareKakao}>
          <IconKakao />
        </button>
        <CopyToClipboard
          text={window.location.origin}
          onCopy={() => {
            window.alert('복사가 완료되었습니다');
          }}
        >
          <button>
            <IconClipBoard />
          </button>
        </CopyToClipboard>
      </div>
    </Section>
  );
}

